<template> 

 <div>
  
  <!-- <p v-if="flagDetail">It is true</p> -->
  <div v-if="flagDetail">
    
    <comp-poke-detail-card :pokemonUrl=pUrl @emitFlagList="showPokemonList"></comp-poke-detail-card>
    
    <!-- <comp-poke-detail :pokemonUrl=pUrl></comp-poke-detail> -->
    
    
  </div>
  <div v-else>
    <comp-poke-list-table @pokeDetail="pokeDetail">
    </comp-poke-list-table>
    
</div>
  </div>

  

</template>

<script>
 import compPokeCardDetail from '../components/pokemonDetailCard.vue'
import pokeListTable from "../components/pokemonListTable.vue"

import { ref } from 'vue'


export default {
components: {
    'comp-poke-list-table': pokeListTable,
    'comp-poke-detail-card' :compPokeCardDetail
},
setup() {
  let flagDetail = ref(false);
  let pUrl = ref();
  function pokeDetail(pokemonUrl) {
    pUrl.value = pokemonUrl;
    flagDetail.value = true;
  }

  function showPokemonList(flagShowDetail) {
    flagDetail.value = flagShowDetail;
  }
  return {
  pokeDetail,
  flagDetail,
  pUrl,
  showPokemonList
  // compPokeCardDetail
} 
},
emits: ['pokeDetail']
}

</script>

