<template>
  <div>
    <base-layout>
   
      <ion-grid>
         <div style="background-color:#cfc5c4">
        <ion-row> 
          <ion-col>
            <center>
              
            <h1 style="font-size:50px; font-family:Hack; color: white; background-color: steelblue;"><strong>PokeVite</strong></h1>
            
            <img style="width:45%;" src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/98/International_Pok%C3%A9mon_logo.svg/1200px-International_Pok%C3%A9mon_logo.svg.png"/>
          </center>
          </ion-col>
        </ion-row>
        <ion-row>
          <ion-col size="3">
            
          </ion-col>
          <ion-col size="6">
            <ion-card v-for="(pokemon, index) in pokemons" :key="index">
        <ion-item class="ion-activated">
          <ion-col size="4">
            
          </ion-col>
          <ion-button
            class="ion-no-padding"
            size="small"
            shape="round"
            @click="pokeDetail(pokemon.url)"
            fill="clear"
          > 
            <Icon size="25" color="red">
              <catching-pokemon-filled />
            </Icon>
            Details
           
          </ion-button>
   
          <ion-label>
           <div style="font-family:Monospace"> {{ pokemon.name.toUpperCase() }} </div>
           </ion-label
          >
        </ion-item>
      </ion-card>
          </ion-col>
          <ion-col size="3">
        
          </ion-col>
           </ion-row>
           </div>
      </ion-grid>

    </base-layout>
  </div>
</template>

<script>
import { onMounted, ref } from "vue";
import {
  IonButton,
  IonCard,
  IonGrid,
  IonRow,
  IonCol,
  // IonIcon,
  IonItem,
  // IonCardContent,
  // IonContent
  // IonLabel,
  // IonList,
  // IonListHeader,
  // IonSelect,
  // IonSelectOption
} from "@ionic/vue";
import { CatchingPokemonFilled } from "@vicons/material";
import { Icon } from "@vicons/utils";

export default {
  components: {
    IonCard,
    // IonIcon,
    IonItem,
    IonGrid,
    IonRow,
    IonCol,
    // IonCardContent,
    // IonContent,
    // IonLabel,
    // IonList,
    // IonListHeader,
    // IonSelect,
    // IonSelectOption,
    // AcUnitFilled,
    Icon,
    CatchingPokemonFilled,
    IonButton,
  },

  setup(props, context) {
    let pokemons = ref([]); // data from api to local

    async function getList() {
      const axios = require("axios");
      await axios
        .get("https://pokeapi.co/api/v2/pokemon?limit=150")
        .then((response) => {
          pokemons.value = response.data.results;
        });
    }

    function pokeDetail(pokemonUrl) {
      // alert(pokemonUrl);
      context.emit("pokeDetail", pokemonUrl);
    }
    onMounted(() => {
      getList();
    });

    return {
      pokemons,
      getList,
      pokeDetail,
    };
  },
  emits: ["pokeDetail"],
};
</script>

