<template>
<div>
  <base-layout>
  
    <link
      href="https://fonts.googleapis.com/icon?family=Material+Icons"
      rel="stylesheet"
    />
    <div>
      <ion-card>
        <ion-card-header>
          <ion-button
            class="ion-no-padding"
            size="small"
            shape="round"
            fill="clear"
            @click="showPokemonList"
          >
            <Icon size="25" color="red">
              <arrow-back-filled />
            </Icon>
            Back
          </ion-button>
          <ion-card-subtitle>Pokemon Details</ion-card-subtitle>

          <ion-card-title color="primary">
            <div v-for="pokemon in pokemonDetail.forms" :key="pokemon">
              <center>
                <h1 style="font-family: Monospace; font-size: 40px">
                  {{ pokemon.name.toUpperCase() }}
                </h1>
              </center>
            </div>
          </ion-card-title>
        </ion-card-header>

        <ion-card-content>
          <div v-for="pokemon in pokemonDetail.forms" :key="pokemon">
            <h1 style="color: red">Id:</h1>
            {{ pokemonDetail.id }}
            <h1 style="color: green">Height:</h1>
            {{ pokemonDetail.height }} feet
            <h1 style="color: blue">Weight:</h1>
            {{ pokemonDetail.weight }} pounds
          </div>
        </ion-card-content>
      </ion-card>

      <ion-grid>
        <div>
        <ion-row>
          <ion-col>
            <div>
              <ion-text color="danger">
                <h2>TYPE</h2>
              </ion-text>
              <div v-for="pokemon in pokemonDetail.types" :key="pokemon">
                <h2 style="font-family: Samanata">
                  {{ pokemon.type.name.charAt(0).toUpperCase()
                  }}{{ pokemon.type.name.slice(1) }}
                </h2>

                 <ion-text color="danger">
                    <h3>BASE EXPERIENCE</h3>
                  </ion-text>  
                    <h2>{{ pokemonDetail.base_experience }}</h2>
              </div>

              <!-- <h2>Base Experience:</h2>
              <ion-text color="primary">{{ pokemonDetail.base_experience }}</ion-text> -->
            </div>
          </ion-col>
          <ion-col>
            <div>
              <ion-text color="secondary">
                <h2>ABILITIES</h2>
              </ion-text>

              <div v-for="pokemon in pokemonDetail.abilities" :key="pokemon">
                <h3 style="font-family: Ubuntu">
                  {{ pokemon.ability.name }}
                </h3>
              </div>
                 
                  
                
              <div v-if="pokemonDetail.held_items > 0">
                <h2>Held Items: {{ pokemonDetail.held_items }}</h2>
              </div>
              <div v-else>
                <h2 style="font-family: Droid Sans Fallback">Pokemon does not seem to have held items</h2>
              </div>
            </div>
          </ion-col>
          <ion-col>
            <div>
              <div v-for="pokemon in pokemonDetail.forms" :key="pokemon">
                <ion-text color="success">
                  <h2>{{ pokemon.name.toUpperCase() }} MOVES</h2>
                </ion-text>
              </div>  
              <ul v-for="(pokemon, index) in pokemonDetail.moves" :key="index">
                <li>{{ pokemon.move.name }}</li>
              </ul>
            </div>
          </ion-col>
        </ion-row>
        </div>
      </ion-grid>
      <footer>All rights reserved</footer>
    </div>
    <!--component to be rendered after getting data from compPokelist-->
    
  </base-layout>
  </div>
</template>

<script>
import { onMounted, ref, reactive } from "vue";
import { ArrowBackFilled } from "@vicons/material";
import { Icon } from "@vicons/utils";

import {
  IonCard,
  IonCardContent,
  IonCardHeader,
  IonCardSubtitle,
  IonCardTitle,
  IonText,
} from "@ionic/vue";

// import {NButton, NModal} from "naive-ui"

export default {
  props: ["pokemonUrl"],
  emits: ["emitFlagList"],
  components: {
    IonCard,
    Icon,
    IonCardContent,
    IonCardHeader,
    IonCardSubtitle,
    IonCardTitle,

    IonText,
    ArrowBackFilled,
  },
  setup(props, context) {
    let pokemonDetail = ref([]);

    let person = reactive({
      name: "",
      email: "",
    });

    function showPokemonList() {
      context.emit("emitFlagList", false);
    }
    async function getLink() {
      await getUrlData();
    }
    onMounted(() => {
      // alert('emit received')
      getUrlData();
    });
    async function getUrlData() {
      const axios = require("axios");
      await axios
        .get(props.pokemonUrl) // get data from props
        .then((response) => {
          pokemonDetail.value = response.data;
        });
    }

    return {
      getLink,
      getUrlData,
      pokemonDetail,
      // checklen
      person,
      showModal: ref(false),
      showPokemonList,
    };
  },
};
</script>

